.planets {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.planet-card {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column-reverse;
  font-size: 25px;
  margin: 10px 0 20px 0;
}

.planet-card {
  transition: 1.5s;
}

.planet-card:hover {
  transform: scale(1.5);
}

.planet-image {
  width: 146px;
}
